.loader {
    position: fixed;
    z-index: 9;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
  /*   position: relative; */
  }
  
    .loader::before,
    .loader::after {
      content: '';
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
    }
  
    .loader::before {
      background: hsla(0, 0%, 100%, 0.7);
    }
  
    .loader::after {
      content: '';
      font-size: 48px;
      width: 1em;
      height: 1em;
      border: 2px solid #ccc;
      border-top-color: #4c91ea;
      border-radius: 50%;
      -webkit-animation: spinner 1s linear infinite;
              animation: spinner 1s linear infinite;
    }
  
    @-webkit-keyframes spinner {
      to { -webkit-transform: rotate(1turn); transform: rotate(1turn); }
    }
  
    @keyframes spinner {
      to { -webkit-transform: rotate(1turn); transform: rotate(1turn); }
    }
