.pagination {
    margin: 0 auto;
    max-width: 1000px;
    width: 100%;
    padding: 0 4px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .pagination__left,
  .pagination__right,
  .pagination__mid {
    padding: 30px 0;
  }
  
  .pagination a,
  .pagination span {
    display: block;
    text-align: center;
    font-family: Helvetica, Arial, sans-serif;
    font-weight: 300;
    line-height: 42px;
    height: 44px;
    color: rgb(85, 85, 85);
    font-size: 18px;
  }
  
  .pagination a {
    padding: 0 16px;
    max-width: 160px;
    background-color: transparent;
    border-radius: 4px;
    border: 1px solid rgb(85, 85, 85);
    text-decoration: none;
    margin: 0 6px;
    -webkit-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }
  
  .pagination a:hover,
  .pagination a.current {
    border-color: rgb(58, 58, 255);
    color: rgb(58, 58, 255);
  }
  
  .pagination__mid {
    margin: 0 auto;
    display: flex;
    justify-content: center;
  }
  
  .pagination__mid ul {
    list-style: none;
    padding: 0;
    margin: 0;
  }
  
  .pagination__mid li {
    display: inline-block;
  }
  
  .hidden {
    display: none !important;
  }
